



$large-device : 1100px;
$medium-device : 765px;

$brand-primary:         #2C3E50 !default;
$brand-success:         #18BC9C !default;
$brand-info:            #3498DB !default;
$brand-warning:         #F39C12 !default;
$brand-danger:          #E74C3C !default;
$brand-default-bg-color: #f5f6f7 !default;


@mixin heading--underline ($color) {
  padding-bottom: 10px;
  border-bottom: 4px solid $color;
  width: inherit;
}

@mixin vertical-padding ($padding) {
  padding-top: $padding;
  padding-bottom: $padding;
}

@mixin horizontal-padding ($padding) {
  padding-left: $padding;
  padding-right: $padding;
}

@mixin heading--left-border ($color) {
  padding-left: 10px;
  border-left: 4px solid $color;
}

@mixin centralize () {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

@mixin triangle-after-section ($color) {
  z-index: 1;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 37px 50px 0 50px;
  border-color: $color transparent transparent transparent;
  position: relative;
  bottom: -37px;
  margin-top: -37px;
  @include centralize();
}

@mixin vertical-padding-separate ($top,$bottom) {
  padding-top: $top;
  padding-bottom: $bottom;
}

@mixin addFadeIn ($seconds) {
  -webkit-animation: fadeIn $seconds; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadeIn $seconds; /* Firefox < 16 */
  -ms-animation: fadeIn $seconds; /* Internet Explorer */
  -o-animation: fadeIn $seconds; /* Opera < 12.1 */
  animation: fadeIn $seconds;
}

@mixin addTransition($attribute, $seconds){
  -webkit-transition: $attribute $seconds; /* Safari */
  transition: $attribute $seconds;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}
/* Firefox < 16 */
@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

body {
  font-size: 16px;
  background-color: $brand-default-bg-color;
}

body.no-nav {
  padding-top: 0px;
}

.full-bg-section {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  background-attachment: fixed;
}


