.navbar-default .navbar-nav>.active>a {
  background-color: $brand-success;
  @include addTransition(background-color,0.5s);
}


.font-14 {
  font-size: 14px;
}

.text-white {
  color: white;
}



.heading--underline-info {
  @include heading--underline($brand-info);
}

.heading--underline-warning {
  @include heading--underline($brand-warning);
}


.heading--underline-danger {
  @include heading--underline($brand-danger);
}

.heading--left-border-info {
  @include heading--left-border($brand-info)
}

.heading--left-border-danger {
  @include heading--left-border($brand-danger)
}

.heading--left-border-warning {
  @include heading--left-border($brand-warning)
}

h2.h2--larger {
  font-size: 39px;
}

.row--space-6 {
  padding-top: 18px;
  padding-bottom: 18px;
}

.btn--hollow-info {
  background-color: transparent;
  border: 3px solid $brand-info;
  color: $brand-info;
}

.btn--hollow-danger {
  background-color: transparent;
  border: 3px solid $brand-danger;
  color: $brand-danger;
}

.btn--flat {
  padding: 5px 20px;
}

nav {
  @include addTransition(background-color,0.5s);
}
.nav-transparent {
  background-color: transparent;
  color: white;
}

//intro page

.slogan {
  background-image: url('../img/mountain-flat-sun.jpg');
  .slogan__content {
    @include addFadeIn(2s);
    color: white;
    left: 50%;
    top: 50%;
    position: absolute;
    text-align: center;
    -webkit-transform: translate3d(-50%,-50%,0);
    -moz-transform: translate3d(-50%,-50%,0);
    -o-transform: translate3d(-50%,-50%,0);
    transform: translate3d(-50%,-50%,0);
    h1 {
      font-size: 64px;
      border-bottom: 2px solid #fff;
      font-weight: bold;
      padding: 20px 0px;
      margin-top: 0px;
      letter-spacing: 2px;
    }
    p {
      font-size: 24px;
    }
    .slogan__title {
      width: 80%;
      border-bottom: 2px solid #fff;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 10px;
      margin-bottom: 0px;
    }
    @media screen and (max-width: $medium-device){
      h1 {
        font-size: 60px;
      }
      p {
        font-size: 20px;
      }
    }
  }
  @media screen and (max-width: $medium-device){
    .slogan__content {
      width: 90%;
    }
  }
}

.brief-intro {
  @include vertical-padding(80px);
  background-color: white;
}



.brief-intro__triangle {
  @include triangle-after-section(white);
}

.project__triangle {
  @include triangle-after-section($brand-default-bg-color);
}


@media screen and (max-width: $medium-device) {
  .profile {
    padding-bottom: 20px;
  }
}

//blog CMS
.sidebar {
  background-color: $brand-primary;
  position: fixed;
  width: 300px;
  height: 100%;
}

.sidebar__menu {
  list-style-type: none;
  padding-left: 0px;
  li {
    a {
      display: block;
      padding: 13px 0px 13px 15px;
    }
  }
}

.sidebar__sub-menu {

}

.project {
  background-color: $brand-default-bg-color;
}

.project__title {
  @include vertical-padding(80px);
}

footer {
  background-color: $brand-primary;
  @include vertical-padding(30px);
}

.bg-shadow-mask {
  background-color: rgba(0,0,0,0.2);
  height: inherit;
}

.ul--large {
  font-size: 20px;
}


.skills {
  color: white;
  @include vertical-padding(200px);
}

@media screen and (max-width: $medium-device) {
  .skills {
    @include vertical-padding(80px);
  }
}

.responsive-container {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}

.profile-photo {
  max-width: 300px;
  @include centralize();
}
.project__thumbnail {
  //box-shadow: 0px 3px 3px #888888;
  -webkit-transition: box-shadow 0.5s;
  transition: box-shadow 0.5s;
  padding: 0px;
  border: none;
  background-color: transparent;
  .caption {
    border-top: 2px $brand-success;
  }
  a {
    img.project__screenshot {
      height: 200px;
    }
  }
}


i.right-margin {
  margin-right: 15px;
}
